import axios from "axios";

const { CORONA_API } = process.env;

class Corona {
  async total() {
    try {
      const data = await axios.get(`${CORONA_API}/all`);
      return data;
    } catch (error) {
      throw new Error(error);
    }
  }

  async countries() {
    try {
      const data = await axios.get(`${CORONA_API}/countries`);
      return data;
    } catch (error) {
      throw new Error(error);
    }
  }
}

export default Corona;
